<template>
  <div class="wrapper">
    <PlayList :songs=songs v-if="songs.length !== 0"></PlayList>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import PlayList from '../../../components/play-list'
export default {
  name: 'index',
  components: {
    PlayList
  },
  data () {
    return {
      songs: []
    }
  },
  created () {
    this.songs = JSON.parse(this.songArr)
  },
  computed: {
    ...mapState('player/song', [
      'songArr'
    ])
  }
}
</script>

<style scoped lang="scss">
.wrapper {
  margin: 30px 0 0;
  height: calc(100% - 66px);
  overflow: hidden;
  overflow-y: auto;
}
</style>
